.form-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 400px;
	height: 500px;
	border: 2px solid black;
	padding-top: 20px;
	border-radius: 10px;
	box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.158);
	background-color: white;
}

.form-container h2 {
	color: #271d83;
	font-weight: bold;
	margin-bottom: 50px;
	margin-top: 20px;
}

.input-field {
	position: relative;
	margin-bottom: 30px;
	width: 300px;
}

.form button {
	padding: 15px 60px;
	margin-top: 40px;
	border: solid 2px #22187c;
	border-radius: 100px;
	background-color: #e5e2ff3f;
	color: #22187c;
	font-weight: bold;
	position: absolute;
	left: 50%;
	transform: translate(-50%, 0px);
	transition: all 0.1s;
	cursor: pointer;
}
.form button:hover {
	background-color: #22187c;
	color: white;
	transform: translate(-50%, -4px);
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.377);
}
.form button:active {
	transform: translate(-50%, -2px);
	box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.377);
}

.form input {
	display: block;
	height: 2.5rem;
	margin: 10px 0;
	border: none;
	border-bottom: solid #271d83 2px;
	padding-left: 5px;
	font-size: 16px;
	color: #271d83;
	width: 100%;
}

.input-field input:placeholder-shown + label {
	color: rgb(0, 0, 0);
	transition: all 0.2s;
	top: 0.7rem;
	pointer-events: none;
	font-size: 16px;
	color: #271d83;
}

.input-field input:focus {
	outline: none;
	border-bottom: solid #271d83 2px;
}

.input-field input:focus + label,
.input-field label {
	left: 5px;
	position: absolute;
	color: #271d8379;
	top: -10px;
	font-size: 12px;
}
