.select-custom {
	display: none;
}

.select-custom-container,
.select-custom-value,
.select-custom-option,
.select-custom-options {
	box-sizing: border-box;
}

.select-custom-container {
	display: inline-block;
	position: relative;
	z-index: 10;
	width: 100%;
}
.select-custom-container:focus > .select-custom-value {
	border-width: 2px;
	outline: none;
}

.select-custom-value {
	display: flex;
	align-items: center;
	justify-content: space-between;

	color: #22187c;
	padding: 0.5rem 0.5rem;
	user-select: none;
	width: 100%;
	background-color: white;
}
.select-custom-value::after {
	content: '';
	display: inline-block;
	border: 4px solid transparent;
	border-top-color: #22187c;
	margin-top: 3px;
	margin-left: 3px;
}

.select-custom-options {
	height: 0px;
	/* display: none; */
	position: absolute;
	padding: 0;
	margin: 0;
	border: 1px solid #22187c;
	border-radius: 5px;

	/* border-top: none; */
	width: 100%;
	/* max-height: 200px; */
	overflow-y: scroll;
	background-color: white;
	color: inherit;
	transition: height 0.7s, border 0.3s;
}
.select-custom-options.show {
	/* display: block; */
	height: 200px;
	border: 2px solid #22187c;
}

.select-custom-option {
	padding: 0.2rem 0.4rem;
	cursor: pointer;
}
.select-custom-option:hover {
	background-color: #837eaf;
	color: white;
}
.select-custom-option.selected {
	background: #22187c;
	color: white;
}

