@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&display=swap);
.form-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 400px;
	height: 500px;
	border: 2px solid black;
	padding-top: 20px;
	border-radius: 10px;
	box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.158);
	background-color: white;
}

.form-container h2 {
	color: #271d83;
	font-weight: bold;
	margin-bottom: 50px;
	margin-top: 20px;
}

.input-field {
	position: relative;
	margin-bottom: 30px;
	width: 300px;
}

.form button {
	padding: 15px 60px;
	margin-top: 40px;
	border: solid 2px #22187c;
	border-radius: 100px;
	background-color: #e5e2ff3f;
	color: #22187c;
	font-weight: bold;
	position: absolute;
	left: 50%;
	-webkit-transform: translate(-50%, 0px);
	        transform: translate(-50%, 0px);
	transition: all 0.1s;
	cursor: pointer;
}
.form button:hover {
	background-color: #22187c;
	color: white;
	-webkit-transform: translate(-50%, -4px);
	        transform: translate(-50%, -4px);
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.377);
}
.form button:active {
	-webkit-transform: translate(-50%, -2px);
	        transform: translate(-50%, -2px);
	box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.377);
}

.form input {
	display: block;
	height: 2.5rem;
	margin: 10px 0;
	border: none;
	border-bottom: solid #271d83 2px;
	padding-left: 5px;
	font-size: 16px;
	color: #271d83;
	width: 100%;
}

.input-field input:-ms-input-placeholder + label {
	color: rgb(0, 0, 0);
	-ms-transition: all 0.2s;
	transition: all 0.2s;
	top: 0.7rem;
	pointer-events: none;
	font-size: 16px;
	color: #271d83;
}

.input-field input:placeholder-shown + label {
	color: rgb(0, 0, 0);
	transition: all 0.2s;
	top: 0.7rem;
	pointer-events: none;
	font-size: 16px;
	color: #271d83;
}

.input-field input:focus {
	outline: none;
	border-bottom: solid #271d83 2px;
}

.input-field input:focus + label,
.input-field label {
	left: 5px;
	position: absolute;
	color: #271d8379;
	top: -10px;
	font-size: 12px;
}

.select-custom {
	display: none;
}

.select-custom-container,
.select-custom-value,
.select-custom-option,
.select-custom-options {
	box-sizing: border-box;
}

.select-custom-container {
	display: inline-block;
	position: relative;
	z-index: 10;
	width: 100%;
}
.select-custom-container:focus > .select-custom-value {
	border-width: 2px;
	outline: none;
}

.select-custom-value {
	display: flex;
	align-items: center;
	justify-content: space-between;

	color: #22187c;
	padding: 0.5rem 0.5rem;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	width: 100%;
	background-color: white;
}
.select-custom-value::after {
	content: '';
	display: inline-block;
	border: 4px solid transparent;
	border-top-color: #22187c;
	margin-top: 3px;
	margin-left: 3px;
}

.select-custom-options {
	height: 0px;
	/* display: none; */
	position: absolute;
	padding: 0;
	margin: 0;
	border: 1px solid #22187c;
	border-radius: 5px;

	/* border-top: none; */
	width: 100%;
	/* max-height: 200px; */
	overflow-y: scroll;
	background-color: white;
	color: inherit;
	transition: height 0.7s, border 0.3s;
}
.select-custom-options.show {
	/* display: block; */
	height: 200px;
	border: 2px solid #22187c;
}

.select-custom-option {
	padding: 0.2rem 0.4rem;
	cursor: pointer;
}
.select-custom-option:hover {
	background-color: #837eaf;
	color: white;
}
.select-custom-option.selected {
	background: #22187c;
	color: white;
}

