.modal-container {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 999;
	height: 100vh;
	width: 100vw;
	/* background: linear-gradient(to left bottom, #6969690e, #31313113); */
	background-color: rgba(54, 54, 54, 0);
	display: flex;
	justify-content: center;
	align-items: center;
	-webkit-backdrop-filter: blur(5px);
	        backdrop-filter: blur(5px);
	transition: all 400ms;
}

.modal-inner {
	width: 50%;
	max-width: 600px;
	height: 50%;
	max-height: 400px;
	background-color: white;
	padding: 50px;
	border-radius: 10px;
	position: relative;
	-webkit-transform: scale(0.8);
	        transform: scale(0.8);
	opacity: 0.9;
	transition: all 400ms;
}

.modal-title {
	text-align: center;
}

.modal-content {
	border: none !important;
	margin-top: 40px;
}

.modal-closebutton {
	font-style: normal;
	font-weight: bold;
	font-size: 22px;
	position: absolute;
	right: 30px;
	top: 30px;
	cursor: pointer;
}

.fade-enter-active,
.fade-enter-done {
	/* background: linear-gradient(to left bottom, #69696948, #3131317a); */
	background-color: rgba(54, 54, 54, 0.637);
